.not-boot-container {
  margin: 0;
}
.login-container {
    display: flex;
    flex-direction: column;
    /* margin: 20%; */
    margin: 20%;
}
.blog-container {
    margin-bottom: 4em;
    margin: 50px 20% 0 20%;
    padding: 1.2em 0px;
}

.blog-container li {
    list-style-type: none;
}

.blog-container p {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.read-more {
    display:flex;
    justify-content: flex-end;
    font-weight: 600;
}
.blog-container ul {
    padding: 0;
}

.blog-container ul, .blog-container p {
    padding: 0;
    width: 80%;
}
/* .scew-container { */
    /* background: black;
    color: white;
} */

.addtask-form{
    display: flex;
    width: 80%;
}
.addtask-form input{
    height: 40px;
    width: 80%;
    font-size: 1em;
}
.addtask-form button{
    width: 20%;
    font-size: 1.2em;
}
.task-container p{
    font-size: 1.2em;
}
.task-container {
    margin: 50px 20% 0 20%;
}
#task-list:hover {
    text-decoration: line-through;
}
.addtask-form button {
    background: black;
    color: white;
    font-size: 1em;
}
.gypsy-axiom h3:nth-child(3){
    text-transform: capitalize;
}

.title-right{
    position: absolute;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    right: 20%;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    -webkit-text-emphasis: triangle;
}
.blog-text {
    /* color: white; */
    padding: 20px 0px 0px 0px;
}
.grey-container {
    display: flex;
    justify-content: space-evenly;
    /* padding: 32px; */
    margin:3em
}

.grey-container h1 {
    font-size: 2.5em;
    margin-bottom: 1.5em;
}
.nav-container {
    display: flex;
    justify-content: space-between;
    margin: 3em 6em;
}
.nav-container ul{
    display: flex;
    list-style-type: none;
}

.nav-container li {
    padding-left: 20px;
}
.nav-container a {
    text-decoration: none;
}
.edit-btn {
    display: flex;
    justify-content: flex-end;
}
.edit-btn p:nth-child(2) {
    margin-left: 20px;
}
.blogEdit-container form{
    display: flex;
flex-direction: column;
width: 100%;
margin-bottom: 2em;
}
.blogEdit-container .save-btn{
    display: flex;
    justify-content: flex-end;
    padding-top: 20px;
}

.save-btn button {
    height: 40px;
    width: 70px;
    font-size: 1em;
}
.blogEdit-container input {
    height: 40px;
}
.blogEdit-container textarea {
    height: 200px;
}
.blogEdit-container h1 {
    padding: .2em 0;
}
